x}.button3:hover span:after{opacity:1;right:0}.navbar{overflow:hidden}.my-card{min-width:260px;max-width:300px;max-height:70vh;border-radius:5px;box-shadow:0px 0px 25px rgba(0,0,0,.1);overflow:hidden;position:relative;margin:10px 30px;cursor:pointer;display:inline-block}.my-card img{transition:all .15s linear;position:relative;top:-15%;left:-25%;filter:brightness(70%)}.my-card .profile-name{position:absolute;top:10px;left:10px;font-size:25px;font-weight:bold;color:#fff;padding:15px 20px;transition:all .15s linear}.my-card .profile-position{position:absolute;color:rgba(255,255,255,.596);left:30px;top:60px;transition:all .15s linear}.my-card .profile-overview{position:absolute;bottom:0px;left:0px;right:0px;background:linear-gradient(0deg, rgba(0, 0, 0, 0.4) 50%, rgba(255, 255, 0, 0));color:#fff;padding:50px 0px 40px 0px;transition:all .15s linear}.my-card .profile-overview h3{font-weight:bold;font-size:large;padding-left:25px}.my-card .profile-overview p{color:rgba(255,255,255,.7);padding-left:25px;padding-right:25px}.my-card:hover .profile-name{padding-left:25px}.my-card:hover .profile-position{left:40px}.my-card:hover .profile-overview{padding-bottom:25px}.my-card img:hover{transform:scale(1.01)}.marvel-background{background-image:linear-gradient(to bottom, rgba(245, 246, 252, 0.774), rgba(66, 55, 64, 0.73)),url(../../assets/login/wallpaperflaretres.jpg);background-size:cover;background-repeat:no-repeat;background-position:center;background-attachment:fixed}.animate__animated.animate__fadeInLeft{--animate-duration: 1.5s}.hero-screen{min-height:93vh;overflow:hidden}.search-screen{min-height:93vh}